@import "_colors.less";

* {
    padding: 0;
	margin: 0;
	font-family: 'Lato', sans-serif;
	box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}
.container {
	width: 940px;
	margin-left: auto;
	margin-right: auto;
    font-size: 10px;
    h3 {
    	font-size: 15px;
    	padding: 4px;
	}
    p {
    	font-size: 13px;
    	text-align: center;
    	padding-top: 1px;
    	padding-bottom: 1px;
    }
    .borders {
    	border: 2px solid black;
    }
    
}

// PRINT BUTTONS
button {
    float: right;
    width: 17%;
    font-size: 16px;
    padding: 10px;
    cursor: pointer;
    margin-top: 20px;
    margin-left: 20px;
    margin-bottom: 20px;
    background-color: white;
    color: black;
    border: 1px solid black;
    font-weight: bold;
}
button:hover {
    transition: 0.65s;
    color: white;
    background-color: @orange;
    border: 1px solid white;
}

// SHEET NUMBERS
.endPage {
	width: 20%;
	float: right;
	margin-top: 15px;
	margin-bottom: 25px;
	text-align: center;
	font-size: 16px;
}
